/* Header Section */
.Main-Container{
    width:100%;
    height:100px;

}
header {
    text-align: center;
    width: 100%;
    display: flex;
    height: 110px;
    line-height: 15px;
    background-color: rgba(251, 159, 21, 1);
}

#name {
    font-size: 45px;
    
}

.name {
    float: right;
    width: 92%;
    height: 110px;
    /* border: solid red 2px; */
}

#logo {
    float: left;
    margin-left: 20px;
}
/* Header Responsive Mobile */
@media screen and (Max-width: 415px) {
    header {
        text-align: center;
        width: fit-content;
        display: flex;
        height: 90px;
        line-height: 15px;
        background-color: rgba(251, 159, 21, 1);
    }
    #name {
        font-size: 15px;
        font-weight:bold;   
    }
    h3{
        font-size:14px;
        font-weight:500;
    }
    .name {
        float: right;
        width: 92%;
        height: 110px;
    }
    #logo {
        float: left;
        margin-left:0px;
    }

  }
/* Header Section Ending */

/* Main Section  */
#MainImg{
    width:100%;
    height:500px;
    display: block;
}
.containerMain{
    height:100%;
    position: relative;
}
.one{
    position: absolute;
    top: 60px;
    color: white;
    font-size: 60px;
    left:80px;
}
.btn-two{
    position: absolute;
    left:80px;
    bottom: 180px;
    background-color:rgba(251, 159, 21, 1) ;
    color: black;
    font-weight: bold;
    width:150px;
    height:40px;
    border:none;
}

/* Main Mobile Responsive  */
@media screen and (Max-width: 415px) {

    #MainImg{
        width:100%;
        height:300px;
        display: block;
    }
.one{
    position: absolute;
    top: 60px;
    color: white;
    font-size: 25px;
    left:30px;
}
.btn-two{
    position: absolute;
    left:30px;
    bottom: 100px;
    background-color:rgba(251, 159, 21, 1) ;
    color: black;
    font-weight: bold;
    width:150px;
    height:40px;
    border:none;
}
}